@media (min-width: 1400px) {
  .container,
  .container-sm,
  .container-md,
  .container-lg,
  .container-xl,
  .container-xxl {
    max-width: 1320px;
  }
}
@media (max-width: 1399px) {
  .service-title {
    padding-left: 0;
  }
  .vision-mission-bg img {
    max-width: 350px;
  }
}
@media (max-width: 1199px) {
  .vital-box {
    margin-bottom: 1rem;
  }
  .portfolio-box h1 {
    font-size: 32px;
  }
  .jointable tbody tr td {
    font-size: 0.9rem;
  }
  .email-box {
    border: 1px solid #a3afb7;
    position: relative;
    border-radius: 4px;
    padding: 1rem 0.5rem 0.5rem;
    margin-top: 1.5rem !important;
  }
  .email-box .icon-box {
    position: absolute;
    top: -15px;
    left: 12px;
    margin-right: 0;
  }
  .vision-mission-bg img {
    max-width: 300px;
  }
}
@media (max-width: 992px) {
  .blog-list {
    position: static;
    width: 90%;
    height: auto;
    margin: 0 auto;
    transform: translateY(-50px);
  }
  .blog-post {
    box-shadow: 0 0 5px lightgray;
    margin: 2rem 0 0;
  }
  .blog-post h2 {
    font-size: 1.2rem;
  }
  .rating p {
    font-size: 1rem;
  }
  .vital-box {
    margin-bottom: 1rem;
  }
  .center-image .blog-list.right-side,
  .center-image .blog-list {
    width: 90%;
  }
  .page-title {
    font-size: 2.5rem;
  }
  .ty-50,
  .ty-25 {
    transform: translateY(0);
    margin-top: 3rem;
  }
  .solution-benefit {
    min-height: 150px;
  }
  .slider-title-text-big {
    font-size: 2.5rem;
  }
  .portfolio-box {
    padding: 1rem;
  }
  .portfolio-box h1 {
    font-size: 24px;
  }
  .number-box {
    width: 160px;
    height: 160px;
  }
  .number-box h1 {
    font-size: 1.5rem;
  }
  .number-box p {
    font-size: 1rem;
  }
  .menu-row {
    height: auto;
  }
  .menu-row .contact-box {
    display: none;
  }
  .menu-column {
    border-right: none;
  }
  .card-columns {
    -webkit-column-count: 2;
    -moz-column-count: 2;
    column-count: 2;
    -webkit-column-gap: 1.25rem;
    -moz-column-gap: 1.25rem;
    column-gap: 1.25rem;
    orphans: 1;
    widows: 1;
  }
  .bg-business {
    height: auto;
  }
  .expertise-box {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
  .expertise-box .line::after {
    content: "";
    border: 0 dashed #f26522;
    position: absolute;
    top: 50%;
    left: 100%;
    height: 2px;
    width: 150%;
  }
  .expertise-box p.font-weight-bold {
    height: auto;
    margin-bottom: 0.5rem !important;
  }
  .cookies-box p {
    font-size: 14px;
    padding-bottom: 0.5rem;
  }
  .b-orange-left,
  .b-orange-right {
    border-top: 3px solid #f26522;
    border-left: none;
    border-right: none;
  }
  .b-orange-left::before {
    top: 0%;
    right: 50%;
    border: 15px solid transparent;
    border-bottom: 15px solid #f26522;
    transform: translateY(-100%) translateX(50%);
  }
  .b-orange-right::before {
    top: 0%;
    left: 50%;
    border: 15px solid transparent;
    border-bottom: 15px solid #f26522;
    transform: translateY(-100%) translateX(-50%);
  }
  .service-icon-size {
    height: 70px;
  }
  .mega-menu-overlay .d-flex.h-100 {
    height: auto !important;
  }
  .green-star::after {
    content: url(../images/right-arrow.svg);
    width: 50px;
    height: 50px;
    position: absolute;
    top: 0%;
    margin-top: -80px;
    left: 50%;
    margin-left: -25px;
  }
  .menu-column.effectline::before {
    content: "";
    display: none;
  }
  .special-menu {
    margin: 0 auto !important;
  }
  .mb-250 {
    margin-bottom: 275px;
  }
  .mb-250.mb-220 {
    margin-bottom: 230px !important;
  }
  .our-location-bg {
    background-position: right;
  }
  .business-navigation li {
    list-style: none;
    display: inline-block;
    margin-right: 7px;
    margin-bottom: 8px;
    padding: 0.5rem;
    min-width: 60px;
    text-align: center;
    background-color: #fbe6dc;
    border-bottom: 0 solid #fbe6dc;
  }
  .business-navigation li.active {
    background-color: #f26522;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    color: #fff;
  }
  .fadearrow i {
    opacity: 1;
  }
  .vision-card {
    transform: translateY(0);
  }
  .mission-card {
    margin-top: 0;
    margin-left: 0;
  }
  .vision-mission-bg {
    display: none;
  }
  .vision-mission-post {
    min-height: 300px;
  }
}
@media (max-width: 767.5px) {
  .header-img {
    background-image: url("../images/mb-slider-back-image.jpg");
  }
  .bg-banner {
    background-image: url("../images/mb-recent-work-bg.jpg");
  }
  .awesome-project {
    background-image: url("../images/mb-awesome-project-back.jpg");
  }
  .we-earn-bg {
    background-image: url("../images/mb-we-earn-back.jpg");
  }
  .com-pt-d1 {
    background-size: 250px 250px;
  }
  .theme-pattern {
    background-size: 80px, 200px, cover;
  }
  .theme-pattern-flip {
    background-size: 80px, 200px, cover;
  }
  .business-slider .owl-nav {
    position: static;
    display: inline-block;
    background-color: #f26522;
    border-radius: 5px;
    transform: translate(28px, -0%);
  }
  .fadeOut .owl-dots {
    margin-top: 0 !important;
  }
  .fadeOut .owl-dots .owl-dot {
    margin-right: 2px;
    margin-left: 2px;
  }
  .slider-title-text {
    font-size: 1.2rem;
  }
  .slider-title-text-big {
    font-size: 2rem;
  }
  .testimonial-user {
    padding: 0;
    position: static;
    margin-top: 1rem;
  }
  .blog-post h2 {
    font-size: 1.2rem;
  }
  .number-box {
    width: 200px;
    height: 200px;
  }
  .page-title {
    font-size: 2rem;
  }
  .page-description {
    font-size: 1rem;
  }
  .section-heading {
    font-size: 1.5rem;
  }
  .awesome-desc {
    font-size: 1.4rem;
  }
  .close-icon-box {
    position: fixed;
  }
  .vertical-line {
    left: 0 !important;
  }
  .b-line-right {
    border-right: 1px solid rgba(143, 143, 143, 1);
    border-left: 3px solid rgba(143, 143, 143, 1);
  }
  .b-line-right::before {
    content: "";
    top: 50%;
    left: auto;
    right: 100%;
    border: 15px solid transparent;
    border-right: 15px solid rgba(143, 143, 143, 1);
  }
  .timeline-point.b-line-right::after {
    content: "";
    position: absolute;
    top: 50%;
    margin-top: -8px;
    right: auto;
    left: -2.6rem;
    width: 16px;
    height: 16px;
    background-color: #fff;
    border-radius: 50%;
    border: 2px solid #f26522;
  }
  .timeline-point.b-line-left::after {
    left: -2.55rem;
  }
  .timeline-point {
    margin: 0.5rem 1rem;
  }
  .pre-next-post li {
    width: 100%;
  }
  .comment__image {
    width: 40px;
    height: 40px;
  }
  .special-menu li {
    transform: rotateY(0deg);
  }
  .outline-white {
    padding: 0;
    border: none;
    background-color: transparent;
  }
  .outline-white::before,
  .outline-white::after {
    display: none;
  }
  .business-title-text {
    font-size: 1.4rem;
  }
  .business-img-box {
    box-shadow: none;
  }
  .case-icon {
    width: 40px;
    height: 40px;
  }
  .casestudy-info {
    font-size: 14px;
  }
  .card-columns {
    -webkit-column-count: 1;
    -moz-column-count: 1;
    column-count: 1;
    -webkit-column-gap: 1.25rem;
    -moz-column-gap: 1.25rem;
    column-gap: 1.25rem;
    orphans: 1;
    widows: 1;
  }
  .casestudy-name {
    font-size: 1.2rem;
  }
}
@media (max-width: 576px) {
  .header-top,
  header .navbar {
    padding-left: 0;
    padding-right: 0;
  }
  .service-title {
    padding-left: 0;
  }
  .number-box {
    width: 50%;
    height: auto;
  }
  .number-box h1 {
    font-size: 1.5rem;
  }
  .number-box p {
    font-size: 1rem;
  }
  .service-card {
    background-color: #fff;
    box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075) !important;
  }
  .back-shape-effect {
    transform: rotateY(0);
  }
  .test-card {
    padding: 2.5rem 1rem 2rem;
  }
  footer .list-group-item {
    display: inline-block;
    border: none;
  }
  .list-group-item.active:before {
    content: "";
    border: 10px solid transparent;
    border-top: 10px solid #f26522;
    position: absolute;
    left: 25px;
    top: 99%;
    transform: translateY(0%);
  }
  .f-flag {
    width: 28px;
    height: auto;
  }
  .jointable tbody tr td {
    font-size: 0.8rem;
  }
  .port-post-photo {
    display: block;
    float: none;
  }
  .port-post-info {
    padding-left: 0;
    margin-top: 20px;
    display: block;
  }
  .port-post-social.float-right {
    float: none !important;
  }
  .port-post-info p {
    margin-top: 10px;
  }
  .port-post-info span {
    font-size: 12px;
    display: block;
  }
  .blog-page-title {
    font-size: 1.5rem;
  }
  .solution-point-title {
    font-weight: 600;
    color: #363636;
    margin-top: 0.75rem;
  }
  .com-pt-d5 {
    background-size: 200px, 100px;
  }
  .com-pt-d5-flip {
    background-size: 100px, 200px;
  }
  .policy-content {
    font-size: 18px;
    text-align: justify;
  }
}
@media (max-width: 359px) {
  .btn-facebook,
  .btn-twitter,
  .btn-linkedin {
    padding: 0.375rem 0.5rem;
  }
  .submit-success-text {
    font-size: 13px;
  }
  .testimonial.owl-theme .owl-dots .owl-dot span {
    width: 10px !important;
    height: 10px !important;
  }
  .testimonial.owl-theme .owl-dots .owl-dot.active span {
    width: 25px !important;
    height: 10px !important;
  }
  .testimonial.owl-theme .owl-item.active .test-card {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  .testimonial.owl-theme .owl-item .test-card {
    box-shadow: none;
  }
  .testimonial.owl-theme .item {
    padding: 0 !important;
  }
  .testimonial.owl-theme .owl-item.active .latestwork-card {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  }
  .testimonial.owl-theme .owl-item .latestwork-card {
    box-shadow: none;
  }
  .g-recaptcha {
    transform: scale(0.85);
    -webkit-transform: scale(0.85);
    transform-origin: 0 0;
    -webkit-transform-origin: 0 0;
  }
  .header-icon-box {
    min-width: 30px;
    margin-left: 6px;
  }
  .masterit-logo {
    width: 180px;
    height: 27px;
  }
}
@media (min-width: 992px) {
  .wrapper.slider {
    height: calc(100vh - 114px) !important;
  }
  .wrapper {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
  }
  .stroke-text {
    animation: textmove 100s linear infinite;
  }
}
@media (max-height: 768px) {
  .wrapper {
    height: auto;
  }
  .wrapper.slider {
    height: auto !important;
  }
}
@media (min-height: 1081px) {
  .wrapper {
    height: auto;
  }
  .wrapper.slider {
    height: auto !important;
  }
}
@media screen and (min-width: 993px) and (max-width: 1199px) {
  .py-150 {
    padding-top: 150px;
    padding-bottom: 150px;
  }
  .py-100 {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  .slider-title-text-big {
    font-size: 3rem;
  }
  .special-menu li {
    width: 90%;
  }
}
@media screen and (min-width: 1200px) and (max-width: 1440px) {
  .py-150 {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  .py-100 {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
@media screen and (min-width: 992px) and (max-width: 1399px) {
  .tech-title {
    height: 80px;
  }
  .case-icon {
    width: 30px;
    height: 30px;
    font-size: 14px;
  }
  .contact-box {
    width: 100%;
  }
  .casestudy-info {
    font-size: 12px;
  }
  .casestudy-name {
    font-size: 1.2rem;
  }
  .service-title h2 {
    font-size: 1.4rem !important;
  }
  .service-card .service-desc {
    font-size: 14px;
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .outline-white::before {
    top: 100px;
  }
  .outline-white::after {
    top: 120px;
  }
  .business-title-text {
    font-size: 1.4rem;
  }
}
@media screen and (min-width: 576px) and (max-width: 767px) {
  .contact-box {
    width: 100%;
  }
}
@media screen and (min-width: 0\0) {
  .theme-pattern {
    background-size: 130px 180px, 300px 300px, cover;
  }
  .theme-pattern-flip {
    background-size: 130px 180px, 300px 300px, cover;
  }
  footer::before {
    content: "";
    position: absolute;
    top: -19px;
    left: 0;
    width: 100%;
    padding: 10px;
    border: none;
    background-color: transparent;
    background: linear-gradient(
      to right,
      rgba(240, 152, 25, 1),
      rgba(255, 81, 47, 1)
    );
    -ms-clip-path: polygon(100% 0, 0% 80%, 0 100%, 100% 100%);
    -o-clip-path: polygon(100% 0, 0% 80%, 0 100%, 100% 100%);
    -webkit-clip-path: polygon(100% 0, 0% 80%, 0 100%, 100% 100%);
    clip-path: polygon(100% 0, 0% 80%, 0 100%, 100% 100%);
    background-image: url("../images/gradient-line-horz.svg");
    background-size: 100% 20px;
  }
  .menu-column.effectline::before {
    content: "";
    position: absolute;
    top: 0;
    left: -1px;
    width: auto;
    height: 100%;
    padding: 0 5px;
    border: none;
    background-color: transparent;
    background: linear-gradient(
      to bottom,
      rgba(240, 152, 25, 1),
      rgba(255, 81, 47, 1)
    );
    -ms-clip-path: polygon(0% 0%, 20% 0%, 100% 100%, 0 100%);
    -o-clip-path: polygon(0% 0%, 20% 0%, 100% 100%, 0 100%);
    -webkit-clip-path: polygon(0% 0%, 20% 0%, 100% 100%, 0 100%);
    clip-path: polygon(0% 0%, 20% 0%, 100% 100%, 0 100%);
    background-image: url("../images/gradient-line-vert.svg");
    background-size: 10px 100%;
  }
}
